<template>
  <div class="hello">
    <p><span>祝</span><span>爸爸</span><span>身</span><span>体</span><span>健</span><span>康</span></p>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted () {
    var winW = document.documentElement.clientWidth; //获取屏幕的宽度
    var desW = 750; //设计稿的宽度
    //设计稿的宽度除以100就是rem的大小
    var rem = desW/100;
    if(winW > desW) { //当屏幕的宽度大于设计稿的宽度
      winW = desW; //让屏幕的宽度等于设计稿的宽度
    }
    document.documentElement.style.fontSize = winW / rem + "px";
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello {
    width: 100%;
    height: 13.24rem;
    background: url(../assets/father.jpg) no-repeat;
    background-size: 100% 100%;
    animation: animate 15s linear;
    overflow: hidden;
  }
  .hello p {
    width: 100%;
    height: 100%;
  }
   .hello p span {
    font-size: .8rem;
    background:url("../assets/3.jpg");
    -webkit-background-clip:text;
    color: rgba(0,0,0,0);
    display: inline-block;
    animation: transform 3s ease reverse;
   }
  @keyframes transform {
    0% {
      margin-top: 0;
    }
    10% {
      margin-top: 0.5rem;
    }
    20% {
      margin-top: -0.5rem;
    }
    100% {
       margin-top: 8rem;
    }

  }
  @keyframes animate {
    0% {
      background: url(../assets/father.jpg) no-repeat;
      background-size: 100% 100%;
    }
    25% {
      background: url(../assets/f2.jpg) no-repeat;
      background-size: 100% 100%;
    }
    50% {
      background: url(../assets/f3.jpg) no-repeat;
      background-size: 100% 100%;
    }
    75% {
      background: url(../assets/f4.jpg) no-repeat;
      background-size: 100% 100%;
    }
    100% {
      background: url(../assets/f5.jpg) no-repeat;
      background-size: 100% 100%;
    }
  }
</style>



